/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #545871;
  font-size: 2.6rem;
  font-weight: 300;
  line-height: 4rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 900;
  text-align: left;
  color: #545871;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 5.6rem;
  line-height: 7rem;
}

h2 {
  font-size: 4.4rem;
  line-height: 5rem;
}

h3 {
  font-size: 3.6rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}
.seccion:last-of-type {
  margin-bottom: 0;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-blanco {
  background-color: #FFFFFF;
  color: #545871;
  font-size: 2.4rem;
  font-weight: 300;
  text-decoration: none;
  padding: 1.5rem 6rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 4rem;
}
.boton-blanco:hover {
  background-color: #f2f2f2;
  cursor: pointer;
}

.boton-gris-oscuro {
  background-color: #545871;
  color: #FFFFFF;
  font-size: 2.4rem;
  font-weight: 300;
  text-decoration: none;
  padding: 1.5rem 6rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 4rem;
}
.boton-gris-oscuro:hover {
  background-color: #494d62;
  cursor: pointer;
}

.boton-rojo {
  background-color: #EBD0CE;
  color: #545871;
  font-size: 2.4rem;
  font-weight: 300;
  text-decoration: none;
  padding: 1.5rem 6rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 4rem;
}
.boton-rojo:hover {
  background-color: #e4bfbc;
  cursor: pointer;
}

.no-webp .inicio {
  background-image: url(../img/header.png);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #545871;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}
.header .circulo {
  background-color: #FFFFFF;
  width: 6.4rem;
  height: 6.4rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .circulo img {
  height: 60%;
  width: 60%;
}
.header .logoTxt {
  color: #FFFFFF;
  padding-left: 2rem;
  font-size: 2.5rem;
  font-weight: 400;
  letter-spacing: 0.3rem;
}
.header .textoBanner {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .header .textoBanner {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header .textoBanner p {
  color: #FFFFFF;
  font-size: 1.6rem;
}
.header .textoGrande {
  width: 40rem;
  padding-bottom: 4rem;
}
.header .textoGrande p {
  font-size: 5.2rem;
  font-weight: 900;
  line-height: 6rem;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: baseline;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
    padding-bottom: 1.2rem;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 5rem;
}
.barra a {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  font-weight: 300;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 2.4rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #B8817D;
}

/** Menú de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.contenedor-experts {
  padding: 8rem 0;
}
.contenedor-experts h1 {
  text-align: left;
}

@media (min-width: 768px) {
  .contenedor-experts {
    display: grid;
    gap: 8rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.boton-gris-oscuro {
  margin-top: 6rem;
}

.gallery .row {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 5rem;
}
.gallery .row .nombre-mascota {
  margin: -5.4rem 0 0 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
}

.no-webp .imagen {
  background-image: url(../img/services.png);
}

.webp .imagen {
  background-image: url(../img/services.webp);
}

@media (min-width: 768px) {
  .contenedor-services {
    display: grid;
    gap: 8rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-services .descTxt {
  padding: 10rem 2rem 10rem 6rem;
}

.imagen {
  background-position: center center;
  background-size: auto 100%;
  background-repeat: no-repeat;
  display: flex;
  align-items: flex-end;
  padding: 0 0 2rem 4rem;
}
.imagen p {
  color: #FFFFFF;
  font-size: 1.6rem;
}

.bg-lila {
  background-color: #F7ECEB;
}

.seccion-large {
  margin: 0;
}

.contenedor-pets {
  background-color: #545871;
  padding: 8rem 0;
}
.contenedor-pets h2 {
  color: #FFFFFF;
  text-align: center;
}
.contenedor-pets button {
  margin: 8rem auto 0 auto;
}

.gallery-pets .row {
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 5rem;
}

.pet-info {
  background-color: #FFFFFF;
  padding: 4rem;
  border-radius: 1rem;
  margin: 0;
  display: grid;
  gap: 3rem;
  grid-template-columns: 1fr 2fr;
}
.pet-info .desc p {
  font-size: 2rem;
  line-height: 2.6rem;
  padding: 0;
  margin: 0;
}

.contenedor-affordable {
  padding: 8rem 0;
}
.contenedor-affordable h2 {
  text-align: center;
  margin-bottom: 3rem;
}
.contenedor-affordable .subtitulo {
  width: 100%;
  text-align: center;
  font-size: 2.4rem;
  color: #545871;
  line-height: 3rem;
}
.contenedor-affordable button {
  margin: 6rem auto 0 auto;
}

.cards {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 10rem;
}

.card {
  border: 1px solid #E6DBDA;
  border-radius: 1rem;
  text-align: center;
}
.card .icono {
  border: 1px solid #E6DBDA;
  border-radius: 50%;
  width: 10rem;
  height: 10rem;
  background-color: #F7ECEB;
  padding: 2rem;
  margin: -5rem auto 4rem auto;
}
.card h3 {
  text-align: center;
}
.card p {
  font-size: 2.2rem;
  line-height: 3rem;
  margin: 0 7rem;
}
.card .price {
  background-color: #F0F1F7;
  width: 100%;
  margin-top: 4rem;
  border-top: 1px solid #E6DBDA;
  padding: 3rem 2rem 2rem 2rem;
  line-height: 3.5rem;
}
.card .price p {
  color: #9597A6;
  font-size: 1.2rem;
  font-weight: 900;
}

.contenedor-contact {
  padding: 8rem 0;
  text-align: center;
}
.contenedor-contact h2 {
  text-align: center;
  color: rgba(84, 88, 113, 0.3);
}
.contenedor-contact p {
  font-size: 4rem;
  font-weight: 900;
}
.contenedor-contact .emailUs {
  text-decoration: underline;
  cursor: pointer;
}